.title{
  display: block;
  margin: 0 auto;
  width: 92%;
}
.title_header{
  font-size: 0.4rem;
  color: #333333;
  margin-bottom: 0.44rem;
}
input{
  font-size: 0.4rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #DDDDDD;
  width: 100%;
  height: 0.8rem;
}
.female, .male, .select{
  height: 1rem;
  line-height: 1rem;
}
.female:visited, .female:active, .male:visited, .male:active{
  background-color: #F9F9F9;
}
input[type="radio"] + label::before,
input[type="checkbox"] + label::before{
  content: "\a0"; /*不换行空格*/
  display: inline-block;
  vertical-align: middle;
  font-size: 0.4rem;
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-right: 0.52rem;
  margin-left: 0.26rem;
  border-radius: 50%;
  border: 2px solid #BBBBBB;
  position: relative;
  bottom: 0.04rem;
}
input[type="checkbox"] + label::before{
  border-radius: 0;
}
input[type="radio"]:checked + label::before,
input[type="checkbox"]:checked + label::before {
  background-color: #0CB8F5;
  /*background-clip: content-box;*/
  /*padding: .2em;*/
}
input[type="radio"],
input[type="checkbox"]{
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
label{
  display: block;
  font-size: 0.4rem;
  color: #666666;
}
.basic{
  display: inline-block;
  font-size: 0.32rem;
  color: #666666;
  height: 0.8rem;
  line-height: 0.8rem;
  position: absolute;
  right: 4%;
}
.query_end{
  width: 100%;
  text-align: center;
  margin-top: 2rem;
}
.query_end img{
  width: 2rem;
  height: 2rem;
}
.query_end p{
  width: 65%;
  margin: 0.5rem auto;
  font-size: 0.36rem;
  color: #666666;
  line-height: 0.5rem;
  /*line-height: 1.6rem;*/
}

.my-radio .am-radio {
  padding: 2.5px;
  border: 1px solid #ccc;
  border-radius: 50%;
  margin-right: 5px;
}


